}
.tariffs .tabs__element li::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, transparent 0%, transparent 25%, transparent 45%, #d10a42 55%, #d10a42 60%, #00359f 100%);
  background-size: 400%;
  transition: background-position 0.25s linear;
}
.tariffs .tabs__element li:hover::before, .tariffs .tabs__element li:focus::before {
  background-position: 100%;
}
.tariffs .tabs__element li:hover .btn, .tariffs .tabs__element li:focus .btn {
  color: #ffffff;
}
.tariffs .tabs__element li:hover .btn::before, .tariffs .tabs__element li:focus .btn::before {
  background-position: 100%;
}

.tariffs .btn {
  margin: auto auto 0;
  padding: 25px 46px;
  line-height: 16px;
  color: #102654;
}
.tariffs .btn::before {
  background-image: linear-gradient(97.05deg, #102654 0%, #102654 40%, #ed0233 72.53%, #003eb7 98.44%);
  transition: 0.3s transform, 0.3s clip-path;
}
.tariffs .btn:hover, .tariffs .btn:focus {
  outline: 2px solid #1c3374;
  outline-offset: #ed0233;
}

